import styled from '@emotion/styled';
import { css } from '@emotion/core';

const primaryStyle = css`
  background-color: #3373fe;
  &:hover {
    background: #007bff;
    border-color: #0151fd;
  }
  &:active {
    background-color: #0151fd;
    border-color: #014cf0;
  }
  &:active:focus {
    box-shadow: 0 0 0 0.2rem rgba(82, 136, 254, 0.5);
  }
`;

const successStyle = css`
  background-color: #0acf97;
  border-color: #0acf97;
  &:hover {
    background-color: #08ab7c;
    border-color: #089e74;
  }
  &:active {
    background-color: #089e74;
    border-color: #07926b;
  }
  &:active:focus {
    box-shadow: 0 0 0 0.2rem rgba(47, 214, 167, 0.5);
  }
`;

const baseView = css`
  background-color: #e7edfc;
  border-color: #e7edfc;
  font-weight: 400;
  &:hover {
    background: #007bff;
    color: #fff;
    border-color: #bacbf6;
  }
  &:active {
    background-color: #bacbf6;
    border-color: #aec3f5;
    color: #212529;
  }
  &:active:focus {
    box-shadow: 0 0 0 0.2rem rgba(201, 207, 220, 0.5);
  }
`;

const borderedView = css`
  background-color: #fff;
  border: 1px solid #3373fe;
  font-weight: 700 !important;
  &:hover {
    background: #007bff;
    color: #fff;
    border-color: #3373fe;
  }
  &:active {
    background-color: #007bff;
    color: #fff;
  }
  &:active:focus {
    box-shadow: 0 0 0 0.2rem rgba(82, 136, 254, 0.5);
  }
`;

const defaultView = css`
  font-weight: 700 !important;
`;

const defaultSize = css`
  padding: 14px 20px;
`;

const largeSize = css`
  padding: 16px 20px;
`;

export default styled('button')`
  border: none;
  outline: none !important;
  font-size: 14px;
  border: 1px solid transparent;

  border-radius: 4px;
  color: ${(p) => p.color};
  ${(p) =>
    !p.disableAnimation &&
    css`
      transition: 0.5s ease, transform 0.25s ease;
      &:hover {
        transform: translate3d(0, -3px, 0);
      }
    `}
 
  ${(p) =>
    p.rounded &&
    css`
      border-radius: 25px;
    `}
  ${(p) => {
    switch (p.paint) {
      case 'primary':
        return primaryStyle;
      case 'success':
        return successStyle;
      default:
        return primaryStyle;
    }
  }}
  ${(p) => {
    switch (p.view) {
      case 'base':
        return baseView;
      case 'bordered':
        return borderedView;
      default:
        return defaultView;
    }
  }}
  ${(p) => {
    switch (p.size) {
      case 'default':
        return defaultSize;
      case 'large':
        return largeSize;
      default:
        return defaultSize;
    }
  }}
`;

export const ButtonGroup = styled('div')`
  &:first-child {
    border-radius: 4px 0 0 4px;
  }
  &:last-child {
    border-radius: 0 4px 4px 0;
  }
`;
