import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import Link from '../Link';
import { Wrapper, Column, Item } from './ColumnList.styles';

class ColumnList extends PureComponent {
  render() {
    const { title, items, ...props } = this.props;
    return (
      <Wrapper {...props}>
        <h5>{title}</h5>
        <Column>
          {items.map((item) => (
            <Item>
              <Link href={item.href}>{item.title}</Link>
            </Item>
          ))}
        </Column>
      </Wrapper>
    );
  }
}

ColumnList.propTypes = {
  title: PropTypes.string,
  items: PropTypes.object, // eslint-disable-line react/forbid-prop-types
};
ColumnList.defaultProps = {
  title: null,
  items: [],
};

export default ColumnList;
