class BottomBlock extends PureComponent {
  constructor(props) {
    super(props);
    const { active, items } = props;
    this.state = {
      active,
      items,
    };
  }

  render() {
    const { title, subtitle, ...props } = this.props;
    const { active, items } = this.state;
    return (
      <Wrapper {...props}>
        <Container>
          <Title>{title}</Title>
          <Subtitle>{subtitle}</Subtitle>
          <Body>
            <ButtonGroup
              style={{
                display: 'flex',
                justifyContent: 'center',
                marginBottom: '3rem',
              }}
            >
              {tabs.map((tab) => (
                <Tab
                  active={active === tab.id}
                  onClick={() =>
                    this.setState({ active: tab.id, items: tab.items })
                  }
                >
                  {tab.title}
                </Tab>
              ))}
            </ButtonGroup>
            <Row style={{ justifyContent: 'center' }}>
              {items.map((item) => (
                <Col xs={12} lg={4}>
                  <Card info={item} />
                </Col>
              ))}
            </Row>
          </Body>
        </Container>
      </Wrapper>
    );
  }
}

BottomBlock.propTypes = {
  title: PropTypes.string,
  subtitle: PropTypes.string,
  active: PropTypes.number,
  items: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.any,
      badge: PropTypes.string,
      footer: PropTypes.string,
      features: PropTypes.array,
      button: PropTypes.any,
      ribbon: PropTypes.string,
      bordered: PropTypes.bool,
    }),
  ),
};
BottomBlock.defaultProps = {
  title: null,
  subtitle: null,
  active: 1,
  items: items1,
};

export default BottomBlock;
