import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { Wrapper, Inner, LeftLine, Triangle, Title } from './Ribbon.styles';

class Ribbon extends PureComponent {
  render() {
    const { title, ...props } = this.props;
    return (
      <Wrapper {...props}>
        <Inner>
          <Title>{title}</Title>
          <LeftLine />
          <Triangle />
        </Inner>
      </Wrapper>
    );
  }
}

Ribbon.propTypes = {
  title: PropTypes.string,
};
Ribbon.defaultProps = {
  title: null,
};

export default Ribbon;
