import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import If from 'react-if';
import Badge from '../Badge';
import Ribbon from '../Ribbon';
import Wrapper, {
  Body,
  Title,
  Feature,
  FeaturesWrapper,
  Footer,
} from './Card.styles';

class Card extends PureComponent {
  render() {
    const { info, ...props } = this.props;
    if (!info) return null;
    return (
      <Wrapper bordered={info.bordered} {...props}>
        <Body>
          <If condition={info.title}>
            <Title>{info.title}</Title>
          </If>
          <If condition={info.badge}>
            <Badge secondary>{info.badge}</Badge>
          </If>
          <If condition={info.features}>
            <FeaturesWrapper>
              {info.features.map((item) => (
                <Feature>{item}</Feature>
              ))}
            </FeaturesWrapper>
          </If>
          <If condition={info.button}>{info.button}</If>
          <If condition={info.footer}>
            <Footer>{info.footer}</Footer>
          </If>
          <If condition={info.ribbon}>
            <Ribbon title={info.ribbon} />
          </If>
        </Body>
      </Wrapper>
    );
  }
}

Card.propTypes = {
  info: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.any,
      badge: PropTypes.string,
      footer: PropTypes.string,
      button: PropTypes.any,
      features: PropTypes.array,
      ribbon: PropTypes.string,
      bordered: PropTypes.bool,
    }),
  ),
};
Card.defaultProps = {
  info: null,
};

export default Card;
